/**
 * Does the work of transmitting data
 * @param {*} guid 
 * @param {*} data 
 */
var Completer = function(guid, data) {
  this.guid = guid;
  this.data = data;
};

/**
 * Save the results to the server
 */
Completer.prototype.saveResults = function(callback) {
  $.post('/s/' + this.guid, this.data, function() {
      
  });
};

/**
 * The CSS overrides
 * Documentation: http://surveyjs.org/examples/jquery/survey-customcss.html
 */
var cssOverrides = {
  "question": {
		"root": "question",
		"title": "qtitle"		
	},
	"navigation": {
		"complete": "navButton complete",
		"prev": "navButton prev",
		"next": "navButton next"
	},
	"progress": "survey-progress",
	"progressBar": "survey-progress-bar"
};
// Controls dynamic behaviors in the survey

var HandleQuestionUpdate = function (surveyNode) {
  $.each(surveyNode.find("input[type=checkbox], input[type=radio]"), function (index, value) {
    value = $(value);
    var qtype = value.attr("type"),
      isChecked = value[0].checked;
      
    if (isChecked) {
      value.parents("label").first().addClass("checked");
      value.parents("div." + qtype).first().addClass("checked");
    } else {
      value.parents("label").first().removeClass("checked");
      value.parents("div." + qtype).first().removeClass("checked");
    }
  });
};
$(document).ready(function () {
  var srvNd = $("script[type=\"finer/survey\"]").first()[0],
    srvB64 = srvNd.innerText,
    srvJSONStr = atob(srvB64),
    srvMdl = JSON.parse(srvJSONStr),
    surveyNode = $("#surveyElement");
  Survey.defaultBootstrapCss.navigationButton = "btn btn-primary";
  Survey.Survey.cssType = "bootstrap";  
  srvMdl.completedHtml = "<div class=\"completion\"></div>";
console.log(srvMdl);
  var survey = new Survey.Model(srvMdl);
  survey.showProgressBar = "top";
  survey.showQuestionNumbers = false;

  var data = { name: "John Doe", email: "johndoe@nobody.com", car: ["Ford"] };
  var surveyValueChanged = function (sender, options) {
    var el = document.getElementById(options.name);
    if (el) {
      el.value = options.value;
    }
  };

  

  surveyNode.Survey({
    "model": survey,
    "data": data,
    "css": cssOverrides,
    "onValueChanged": function() {
      HandleQuestionUpdate(surveyNode);
    },
    "onComplete": function(srv) {
      var dta = srv.data;
      var cmp = new Completer(srvMdl.guid, dta);
      cmp.saveResults();
    }
  });

});